function Body(props) {

  return (
    <div className={'w-100 bg-'+props.theme+' text-'+props.themeOpposite()} style={{height:'100vh'}} >
      <div className={'container bg-'+props.theme} style={{ height: '75vh' }} >
        <div className="row h-100 pt-5 pt-md-0">
          {props.children}
        </div>
        <div className='d-flex justify-content-center' >
          <a onClick={props.action} className={'btn btn-'+props.themeOpposite()}>Change theme</a>
        </div>
      </div>
    </div>
  )
}

export default Body