import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import '../Custom.css'

function Card(props) {
  let source = props.source
  props.source ? null : source = 'https://picsum.photos/420/300'
  return (
    <Link className="col-6 nolink col-md-4 ml-2 col-lg-3 p-0 my-auto mx-auto text-center border border-dark rounded" to={props.card} >
      <div className={'text-'+props.theme()} >
        <div className='m-0 px-auto w-100' >
          <img src={source} className='w-100 rounded-top' alt="" />
        </div>
        <div>
          <h5>{props.title}</h5>
          <p>{props.text}</p>
        </div>
      </div>
    </Link>
  )
} 

export default Card